.btn {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid transparent;
  white-space: nowrap;
  padding: 10px 30px;
  border-radius: $border-radius;
  background: $gray;
  user-select: none;
  &.btn-disabled {
    cursor: default;
    opacity: .35;
    box-shadow: none;
    pointer-events: none;
  }
}
.btn-primary {
  background-color: $brand-primary;
  color: #fff !important;
  &:hover {
    background: darken($brand-primary, 10%);
  }
}
.btn-block {
  display: block;
  width: 50%;
  margin: 0 auto !important;
  max-width: 320px;
}
