.adtoniq-messenger-form {
  margin-top: 30px;
  @extend .well;
  .wp-editor-wrap {
    margin-top: -33px;
  }
}

.adtoniq-mce-container {
  position: relative;
  padding: 0 20px;
}

label[for="adtoniqMsgMessage"] {
  position: absolute;
  top: 0;
  left: 20px;
}

.form-control,
input[type="number"].form-control {
  display: block;
  width: 100%;
  height: $input-height;
  padding: 6px 12px;
  color: $gray-dark;
  background-color: #fff;
  border: 1px solid $border-color;
  border-radius: $border-radius;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
  &:focus {
    outline: 0;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(255, 0, 0, .075);
  }
  &::-moz-placeholder {
    color: $gray-light;
    opacity: 1;
  }
  &:-ms-input-placeholder { color: $gray-light; }
  &::-webkit-input-placeholder  { color: $gray-light; }

  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    color: $gray;
    background-color: $input-bg-disabled;
    opacity: 1; // iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655
  }

  &[disabled],
  fieldset[disabled] & {
    cursor: not-allowed;
  }
}

.form-horizontal {
  .control-label {
    text-align: right;
    margin-bottom: 0;
    padding-top: 7px;
  }
  .form-group {
    position: relative;
    margin-left: -10px;
    margin-right: -10px;
    padding-bottom: 25px;
    overflow: hidden;
  }
}

.help-block {
  display: none;
  position: absolute;
  top: ($input-height + 2px);
  left: calc(16.33333% + 13px);
  line-height: 1;
  margin-top: 5px;
  margin-bottom: 10px;
  color: $gray;
  font-size: 12px;
}

.has-feedback {
  position: relative;
  .form-control {
    padding-right: 43px;
  }
  .form-control-feedback {
    display: block;
  }
}

.form-control-feedback {
  position: absolute;
  display: none;
  top: 0;
  right: 10px;
  z-index: 2;
  width: $input-height;
  height: $input-height;
  line-height: $input-height;
  text-align: center;
  pointer-events: none;
}

.has-success {
  .form-control,
  input[type="number"].form-control {
    border-color: $brand-success;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    &:focus {
      border-color: darken($brand-success, 10%);
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px lighten($brand-success, 20%);
    }
  }
  .form-control-feedback  {
    color: $brand-success;
  }
}

.has-error {
  .form-control,
  input[type="number"].form-control {
    border-color: $brand-danger;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    &:focus {
      border-color: darken($brand-danger, 10%);
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px lighten($brand-danger, 20%);
    }
  }
  .help-block,
  .control-label,
  .form-control-feedback {
    color: $brand-danger;
  }
}

.has-warning {
  .form-control {
    border-color: $brand-warning;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    &:focus {
      border-color: darken($brand-warning, 10%);
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px lighten($brand-warning, 20%);
    }
  }
  .help-block,
  .control-label,
  .form-control-feedback {
    color: $brand-warning;
  }
}

.input-group {
  position: relative; // For dropdowns
  display: table;
  border-collapse: separate; // prevent input groups from inheriting border styles from table cells when placed within a table

  &[class*="col-"] {
    float: none;
    padding-left: 0;
    padding-right: 0;
  }

  .form-control {
    position: relative;
    z-index: 2;
    float: left;
    width: 100%;
    margin-bottom: 0;
    &:focus {
      z-index: 3;
    }
  }
}
// Display as table-cell
// -------------------------
.input-group-addon,
.input-group .form-control {
  display: table-cell;
  &:not(:first-child):not(:last-child) {
    border-radius: 0;
  }
}
// Addon and addon wrapper for buttons
.input-group-addon {
  width: 1%;
  white-space: nowrap;
  vertical-align: middle; // Match the inputs
}

// Text input groups
// -------------------------
.input-group-addon {
  padding: 6px 12px;
  font-size: 14px;
  font-weight: normal;
  line-height: 1;
  color: $gray-dark;
  text-align: center;
  background-color: $gray-lighter;
  border: 1px solid $border-color;
  border-radius: $border-radius;
}

.input-group-addon:first-child {
  border-right: 0;
}
.input-group-addon:last-child {
  border-left: 0;
}

.radio,
.checkbox {
  position: relative;
  display: block;
  margin-top: 10px;
  margin-bottom: 10px;
  label {
    min-height: 20px;
    padding-left: 20px;
    margin-bottom: 0;
    font-weight: 400;
    cursor: pointer;
  }
}

.radio input[type="radio"],
.checkbox input[type="checkbox"]{
  position: absolute;
  margin-top: 4px \9;
  margin-left: -20px;
}
.radio + .radio,
.checkbox + .checkbox {
  margin-top: -5px;
}

// adjustments for bottom field group
#adtoniqMsgButtonFields {
  padding-top: 30px;
  .form-group {
    padding-bottom: 30px;
  }
}
