.adtoniq-notice {
  display: inline-block;
  width: 100%;
  margin: 20px 0;
  padding: 10px;
  font-size: 13px;
  background-color: #fff;
  border-left: 10px solid $brand-success;
}

.alert {
  position: relative;
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: $border-radius;
  h4 {
    margin-top: 0;
    margin-bottom: 5px;
    color: inherit;
  }
  > p,
  > ul {
    margin-top: 5px;
    margin-bottom: 0;
    list-style: disc !important;
  }
  > ul {
    margin-top: 0;
    padding-left: 20px;
  }
  > p + p {
    margin-top: 5px;
  }
  .btn {
    margin: 10px 0;
  }
  &.with-icon {
    padding-left: 55px;
    [class*="icon-"] {
      position: absolute;
      left: 10px;
      width: 32px;
      height: 32px;
      line-height: 32px;
      margin-top: -16px;
      font-size: 32px;
      transform: translateY(73%);
    }
  }
}

.alert-error {
  background-color: $state-danger-bg;
  color: $state-danger-text;
  border-color: $state-danger-border;
  strong {
    color: darken($state-danger-text, 15%);
  }
}

.alert-warning {
  background-color: $state-warning-bg;
  color: $state-warning-text;
  border-color: $state-warning-border;
  strong {
    color: darken($state-warning-text, 15%);
  }
}

.alert-success {
  background-color: $state-success-bg;
  color: $state-success-text;
  border-color: $state-success-border;
  strong {
    color: darken($state-success-text, 15%);
  }
}

.muted {
  color: #aaa;
}

// wordpress overrides

.adtoniq-plugin .notice-dismiss:before {
  color: #fff;
}

.adtoniq-plugin .notice-success {
  background: #46b450;
  color: #fff;
}
