$brand-primary:       #395972;
$brand-secondary:     #7a0600;
$brand-neutral:       #6e6a61;
$brand-success:       #227f57;
$brand-warning:       #fdbf33;
$brand-danger:        #7a0600;
$brand-info:          #40a3e5;

$text-color:          #4a4a4a;
$link-color:          $brand-info;
$link-color-hover:    darken($link-color, 10%);
$border-color:        #d7d7d7;
$gray-dark:           #666;
$gray:                #999;
$gray-muted:          #aaa;
$gray-light:          #ccc;
$gray-lighter:        #f3f3f3;

$input-height:        34px;
$border-radius:       0;

$state-danger-text:   #a94442 !default;
$state-danger-bg:     #f2dede !default;
$state-danger-border: darken(adjust-hue($state-danger-bg, -10), 5%) !default;

$state-warning-text:             #b98b26 !default;
$state-warning-bg:               #fee1a2 !default;
$state-warning-border:           darken(adjust-hue($state-warning-bg, -10), 5%) !default;

$state-success-text:             #1c6848 !default;
$state-success-bg:               #c2dcd1 !default;
$state-success-border:           darken(adjust-hue($state-success-bg, -10), 5%) !default;

$input-bg-disabled:              $gray-lighter !default;
