.nav {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  > li {
    position: relative;
    display: block;
    > a {
      position: relative;
      display: block;
      padding: 10px 15px;
      text-decoration: none;
      &:hover,
      &:focus {
        text-decoration: none;
        background-color: $border-color;
      }
    }
  }
}

.nav > li.disabled > a {
  color: $gray-dark;
}
.nav > li.disabled > a:hover,
.nav > li.disabled > a:focus {
  color: $gray;
  text-decoration: none;
  cursor: not-allowed;
  background-color: transparent;
}
.nav .open > a,
.nav .open > a:hover,
.nav .open > a:focus {
  background-color: $gray-light;
}
.nav-tabs {
  border-bottom: 1px solid $border-color;
  margin-bottom: -1px;
  z-index: 9999;
  > li {
    float: left;
    margin-bottom: -1px;
    > a {
      margin-right: 2px;
      line-height: 1.42857143;
      border: 1px solid transparent;
      border-radius: 4px 4px 0 0;
      &:hover {
        border-color: lighten($border-color, 10%) lighten($border-color, 10%) $border-color;
      }
    }
  }
}

.nav-tabs > li.active > a,
.nav-tabs > li.active > a:hover,
.nav-tabs > li.active > a:focus {
  color: $gray-dark;
  cursor: default;
  background-color: #fff;
  border: 1px solid $border-color;
  border-bottom-color: transparent;
}

.tab-content {
  > .tab-pane {
    display: none;
  }
  > .active {
    display: block;
  }
}
