@font-face {
  font-family: 'icons';
  src: url('data:application/font-woff;base64,') format('woff');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"],
[class*=" icon-"] {
  font-family: 'icons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-ok:before {
  content: "\f00c";
}

.icon-error:before {
  content: "\f00d";
}

.icon-warning:before {
  content: "\f071";
}
