.adtoniq-plugin {
  padding: 20px 0;
  font-size: 13px;
  line-height: 1.5;
  font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  color: $text-color;
  * {
    box-sizing: border-box;
  }
  a {
    color: $link-color !important;
    cursor: pointer;
    &:hover {
      color: $link-color-hover !important;
    }
  }
  h2 {
    margin: 0 0 5px;
    font-size: 18px;
  }
  input {
    color: inherit;
    font: inherit;
    margin: 0;
    line-height: normal;
  }
  input[type="button"] {
    -webkit-appearance: button;
    cursor: pointer;
  }
  label {
    display: inline-block;
    max-width: 100%;
    margin-bottom: 5px;
    font-weight: bold;
  }
  small,
  .small {
    font-size: 65%;
  }
  fieldset {
    min-width: 0;
    padding: 20px 0;
    margin-bottom: 20px;
    border: 1px solid #d7d7d7;
  }
  legend {
    margin-left: 10px;
    padding: 0 8px;
    font-size: 16px;
    line-height: inherit;
    white-space: normal;
    font-weight: 700;
    color: #999;
  }
  .lead {
    font-size: 18px;
    font-weight: 300;
  }
  .version-number {
    position: absolute;
    top: 45%;
    margin-left: 10px;
  }
  pre {
    margin: 0;
    padding: 8px;
    font-size: 12px;
    white-space: normal;
    background: #f3f3f3;
  }
}

.adtoniq-logo-header {
  position: relative;
}
.adtoniq-logo {
  display: inline-block;
  margin-bottom: 10px;
  width: 250px;
}
.adtoniq-cyan-shortcode {
  margin: 10px 0;
  padding: 4px 8px;
  min-height: 32px;
  border: 5px solid cyan;
}
