[class*="col-"] {
  position: relative;
  float: left;
  min-height: 1px;
  padding-left: 10px;
  padding-right: 10px;
  &.main {
    @media (max-width: 959px) {
      width: 100%;
      min-width: 600px;
    }
  }
}
.row {
  position: relative;
  margin-right: -10px;
  margin-left: -10px;
  overflow: hidden;
}
.col-xs {
  width: 16.666667%;
}
.col-sm {
  width: 33.333333%;
}

.col-half {
  width: 50%;
}
.col-md {
  width: 100%;
}
.col-lg {
  width: 83.3333%;
}
.col-full {
  width: 100%;
}
.col-xs-offset {
  margin-left: 16.66667%;
}
.col-sm-offset {
  margin-left: 33.333333%;
}
