.premium-header {
  .page-header {
    margin: 40px 0 20px;
    font-size: 42px;
    padding-bottom: 19px;
    small {
      font-size: 16px;
    }
  }
}
.premium-anchor-link {
  display: none;
  position: absolute;
  right: 10px;
  top: 24px;
  text-decoration: none;
}
.premium-anchor-link:hover {
  text-decoration: underline;
}
@media (min-width: 480px) {
  .premium-anchor-link {
    display: inline-block;
  }
}

#anal-iframe {
  width: 100%;
  min-height: 300px;
  height: auto;
}
